import { Component, OnInit } from '@angular/core';
import { Summary, Education } from 'src/app/entities/summary';
import { SummaryResumeService } from 'src/app/services/summary-resume/summary-resume.service';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.scss']
})
export class UserProfileComponent implements OnInit {

  constructor(private _summaryResumeService: SummaryResumeService) { }
  public Summary: Summary;
  ngOnInit() {
    this.getData();
  }

  private getData(): void {
    this._summaryResumeService.GetProfile().subscribe(data => {
      this.Summary = data;
    });
  }

  public EducationPrincipal(): Education {
    return this.Summary.Education[0];
  }

  public CalcularEdad(): number {
    var hoy = new Date();
    var cumpleanos = new Date(this.Summary.BirthdayDate);
    var edad = hoy.getFullYear() - cumpleanos.getFullYear();
    var m = hoy.getMonth() - cumpleanos.getMonth();

    if (m < 0 || (m === 0 && hoy.getDate() < cumpleanos.getDate())) {
      edad--;
    }

    return edad;
  }

  private classes = ["bg-gradient-danger", "bg-gradient-success", "bg-gradient-primary", "bg-gradient-info", "bg-gradient-warning"]
  public GetClassForSkill(): string {
    return this.classes[this.RandomNumber(0,5)]
  }

  private RandomNumber(a, b): number {
    return Math.round(Math.random() * (b - a) + parseInt(a));
  }
}
